@media (min-width: 768px) {
  header {
    height: 80px;
  }
  h3 {
    padding-left: 5px;
  }
  body {
    display: grid;
  }
  .grid-container {
    grid-template-columns: auto 1fr;
    grid-template-rows: auto;
    grid-template-areas:
      "header header"
      "nav main";
  }
  header {
    display: flex;
    h1 {
      padding-left: 0.938rem;
      padding-right: 26.25rem;
    }
  }
  .nav-container {
    grid-row-start: 2;
    grid-column-start: 1;
  }
  nav {
    display: table-cell;
    height: 130.2rem;
    width: 90px;
  }
  .navigation {
    display: inline;
  }
  .light-grey-tab {
    width: 80%;
  }
  .traffic-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  li {
    padding: 10px;
  }
  #traffic-header {
    padding-left: 10px;
  }
  .traffic-nav{
    padding-top: 0;
  }
  .social-stats {
    display: flex;
  }
  .facebook-stats {
    margin: 10px 5px 10px 10px;
  }
  .instagram-stats {
    margin: 10px 5px 10px 5px;
  }
  .twitter-stats {
    margin: 10px 10px 10px 5px;
  }
  .new-members,
  .recent-activity {
    display: flex;
    flex-direction: column;
    img {
      padding-right: 15px;
    }
  }
  #Victoria-Chambers,
  #Dale-Byrd,
  #Dawn-Wood,
  #Dan-Oliver,
  #Victoria-Chambers-recent-activity,
  #Dale-Byrd-recent-activity,
  #Dawn-Wood-recent-activity,
  #Dan-Oliver-recent-activity {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
  }

  #Victoria-Chambers p,
  #Dale-Byrd p,
  #Dawn-Wood p,
  #Dan-Oliver p {
    text-align: left;
  }

  #Victoria-Chambers a {
    padding-right: 13.625rem;
  }

  #Dale-Byrd a,
  #Dawn-Wood a {
    padding-right: 18rem;
  }

  #Dan-Oliver a {
    padding-right: 17.375rem;
  }

  #Dan-Oliver {
    margin-bottom: 15px;
  }

  #Victoria-Chambers a,
  #Dale-Byrd a,
  #Dawn-Wood a,
  #Dan-Oliver a {
    display: inline;
  }

  hr {
    margin-left: 0;
    margin-right: 0;
  }

  .recent-activity p {
    text-align: left;
  }

  #send-email-notifactions-header,
  #set-profile-to-public-toggle {
    padding-left: 5px;
  }
  .settings-slider {
    max-width: 100px;
  }
  .traffic {
    grid-area: traffic;
  }
  .daily-traffic {
   grid-area: dailyTraffic;
  }
  .mobile-users {
    grid-area: mobileUsers;
  }
  .daily-traffic,
  .mobile-users {
  width: 50%;
  float: left;
  }
}


@media (min-width: 1024px) {
 .header-container h1 {
   padding-right: 42.25rem;
 }
 .headline {
   padding: 0;
   margin: 0 auto;
 }
}

